<template>
  <v-container class="pt-10">
    <v-sheet class="admin-login" width="500">
      <v-form @submit.prevent="handleLogin">
        <div class="form-group">
          <v-text-field
            label="管理者ID"
            v-model="adminId"
            type="text"
            placeholder=""
            outlined
            :error="!!idError"
            :error-messages="idError"
            required
            />
          </div>
        <div class="form-group">
          <v-text-field
            label="パスワード"
            v-model="password"
            :type="visiblePass ? 'text' : 'password'"
            outlined
            :error="!!passwordError"
            :error-messages="passwordError"
            placeholder=""
            :append-inner-icon="visiblePass ? 'mdi-eye-off-outline' : 'mdi-eye-outline'"
            @click:append-inner="visiblePass = !visiblePass"
            required
          />
        </div>
        <v-btn type="submit" class="login-button" size="large" append-icon="mdi-login">ログイン</v-btn>
      </v-form>
    </v-sheet>
  </v-container>
</template>

<script>
export default {
  name: "AdminLogin",
  data() {
    return {
      adminId: "",
      password: "",
      idError: null,
      passwordError: null,
      visiblePass: false,
    };
  },
  methods: {
    handleLogin() {
      // バリデーションチェック
      this.idError = null;
      this.passwordError = null;

      if (!this.validateId(this.adminId)) {
        this.idError = "Please enter a valid ID.";
      }

      if (!this.idError && !this.passwordError) {
        console.log("Logging in with:", {
          adminId: this.adminId,
          password: this.password,
        });
        this.$router.push('/admin'); // 管理者ページに遷移
      }
    },
    validateId(id) {
      return id && id !== '';
    },
  },
};
</script>

